import React, { createContext, useReducer, useEffect } from 'react'
import EstatePlanReducer from './EstatePlanReducer'
import CryptoJs from 'crypto-js'

const initialState = {
    grantor: {},
    cryptoassets: {
        wallets: [],
        trustedHelpers: [],
        typesHeld: [],
        otherInformation: '',
    },
    contacts: [],
    devices: [],
    documents: [],
}

export const EstatePlanContext = createContext()

const EstatePlanDataStorageKey = 'estate_plan_data'
const encryptionKey = 'secret'

const EstatePlanStore = ({ children }) => {
    const [state, reducer] = useReducer(EstatePlanReducer, initialState, () => {
        try {
            const cyphertext = localStorage.getItem(EstatePlanDataStorageKey)
            if (cyphertext && cyphertext !== 'null') {
                const json = CryptoJs.AES.decrypt(cyphertext, encryptionKey).toString(CryptoJs.enc.Utf8)
                return JSON.parse(json)
            } else {
                return {...initialState}
            }
        } catch (e) {
            return initialState
        }

    })

    useEffect(() => {
        const json = JSON.stringify(state);
        const encrypted = CryptoJs.AES.encrypt(json, encryptionKey).toString()
        localStorage.setItem(EstatePlanDataStorageKey, encrypted)
    }, [state])

    return (
        <EstatePlanContext.Provider value={[state, reducer]}>
            {children}
        </EstatePlanContext.Provider>
    )
}

export default EstatePlanStore
