import React, { useContext } from 'react'
import { Form, FormControl, FormGroup, FormLabel } from 'react-bootstrap'
import { EstatePlanContext } from '../../State/EstatePlanStore'
import { UPDATE_BY_PATH } from '../../State/EstatePlanReducer'
import BasePage from '../BasePage'
import CryptoassetsNav from "./CryptoassetsNav"

const CryptoassetsOtherThingsYouShouldKnow = () => {
    const owlText = (
        <>
            Is there anything else that you want your heirs to know about your
            cryptoassets?
        </>
    )

    const [
        {
            cryptoassets: { otherInformation },
        },
        dispatch,
    ] = useContext(EstatePlanContext)

    const onChange = ({ target: { id, value } }) => {
        dispatch({
            type: UPDATE_BY_PATH,
            path: `cryptoassets.${id}`,
            value: value,
        })
    }

    return (
        <BasePage owlSay={owlText}>
            <CryptoassetsNav />
            <Form onSubmit={({ preventDefault }) => preventDefault()}>
                <FormGroup controlId="otherInformation">
                    <FormLabel>Other information for your heirs</FormLabel>
                    <FormControl
                        value={otherInformation}
                        onChange={onChange}
                        as="textarea"
                        rows="5"
                    />
                </FormGroup>
            </Form>
        </BasePage>
    )
}

CryptoassetsOtherThingsYouShouldKnow.path = '/cryptoassets/other'

export default CryptoassetsOtherThingsYouShouldKnow
